useEffect(() => {
    const canvas = canvasRef.current
    if (!canvas) return

    const ctx = canvas.getContext("2d")
    if (!ctx) return

    canvas.width = canvas.offsetWidth
    canvas.height = canvas.offsetHeight

    const handleMouseMove = (e: MouseEvent) => {
      const rect = canvas.getBoundingClientRect()
      mouseRef.current = {
        x: e.clientX - rect.left,
        y: e.clientY - rect.top,
      }
    }

    window.addEventListener("mousemove", handleMouseMove)

    // Create grid of morphing shapes
    const gridSize = 8
    const shapes: Array<{
      x: number
      y: number
      size: number
      phase: number
      speed: number
    }> = []

    for (let i = 0; i < gridSize; i++) {
      for (let j = 0; j < gridSize; j++) {
        shapes.push({
          x: (canvas.width / gridSize) * i + canvas.width / gridSize / 2,
          y: (canvas.height / gridSize) * j + canvas.height / gridSize / 2,
          size: Math.min(canvas.width, canvas.height) / gridSize * 0.6,
          phase: Math.random() * Math.PI * 2,
          speed: 0.3 + Math.random() * 0.4,
        })
      }
    }

    const drawShapes = (time: number) => {
      ctx.clearRect(0, 0, canvas.width, canvas.height)

      shapes.forEach((shape) => {
        const dx = mouseRef.current.x - shape.x
        const dy = mouseRef.current.y - shape.y
        const dist = Math.sqrt(dx * dx + dy * dy)
        const maxDist = 300

        // Morph between square and circle based on time and mouse proximity
        const morphAmount = Math.sin(time * 0.001 * shape.speed + shape.phase) * 0.5 + 0.5
        const mouseInfluence = Math.max(0, 1 - dist / maxDist) * 0.3
        const roundness = morphAmount + mouseInfluence

        // Glassmorphic gradient
        const gradient = ctx.createRadialGradient(shape.x, shape.y, 0, shape.x, shape.y, shape.size / 2)
        gradient.addColorStop(0, `rgba(255, 255, 255, ${0.03 + mouseInfluence * 0.05})`)
        gradient.addColorStop(1, `rgba(255, 255, 255, 0.01)`)

        ctx.fillStyle = gradient
        ctx.strokeStyle = `rgba(255, 255, 255, ${0.08 + mouseInfluence * 0.12})`
        ctx.lineWidth = 1.5

        // Draw morphing shape
        const size = shape.size * (1 + mouseInfluence * 0.15)
        const radius = size / 2
        const cornerRadius = radius * roundness

        ctx.beginPath()
        ctx.moveTo(shape.x - radius + cornerRadius, shape.y - radius)
        ctx.arcTo(shape.x + radius, shape.y - radius, shape.x + radius, shape.y + radius, cornerRadius)
        ctx.arcTo(shape.x + radius, shape.y + radius, shape.x - radius, shape.y + radius, cornerRadius)
        ctx.arcTo(shape.x - radius, shape.y + radius, shape.x - radius, shape.y - radius, cornerRadius)
        ctx.arcTo(shape.x - radius, shape.y - radius, shape.x + radius, shape.y - radius, cornerRadius)
        ctx.closePath()

        ctx.fill()
        ctx.stroke()
      })
    }

    let animationId: number
    const animate = (time: number) => {
      drawShapes(time)
      animationId = requestAnimationFrame(animate)
    }

    animationId = requestAnimationFrame(animate)

    return () => {
      window.removeEventListener("mousemove", handleMouseMove)
      cancelAnimationFrame(animationId)
    }
  }, [])